<template>
<div>

  <div v-if="$store.state.auth.user">
    <div class="navbar_bottom" >
      <div>
        <button class="clear_btn" @click="getReload()">

          <svg width="18" height="18" viewBox="0 0 18 18" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M0.75 9C0.75 9.41421 1.08579 9.75 1.5 9.75C1.91421 9.75 2.25 9.41421 2.25 9H0.75ZM17.25 9C17.25 8.58579 16.9142 8.25 16.5 8.25C16.0858 8.25 15.75 8.58579 15.75 9H17.25ZM3.87004 13.3872C3.60066 13.0726 3.12721 13.0359 2.81255 13.3053C2.4979 13.5746 2.4612 14.0481 2.73058 14.3628L3.87004 13.3872ZM14.13 4.61276C14.3993 4.92741 14.8728 4.96411 15.1874 4.69473C15.5021 4.42535 15.5388 3.9519 15.2694 3.63724L14.13 4.61276ZM16.242 0.746654C16.2402 0.332444 15.9029 -0.00184059 15.4887 7.45058e-06C15.0745 0.00185555 14.7402 0.339137 14.742 0.753346L16.242 0.746654ZM15.4987 2.23928L16.2486 2.23594V2.23594L15.4987 2.23928ZM12.7387 4.99928L12.7353 5.74928L12.7387 4.99928ZM11.2527 4.24265C10.8385 4.2408 10.5012 4.57508 10.4994 4.98929C10.4975 5.4035 10.8318 5.74078 11.246 5.74263L11.2527 4.24265ZM15.1922 4.427L15.7854 4.88602L15.7854 4.88602L15.1922 4.427ZM14.9264 4.69287L15.3854 5.286L15.3854 5.286L14.9264 4.69287ZM1.69639 17.193C1.69824 17.6072 2.03552 17.9415 2.44973 17.9397C2.86394 17.9378 3.19822 17.6006 3.19638 17.1863L1.69639 17.193ZM2.43974 15.7004L1.68975 15.7038L2.43974 15.7004ZM5.19974 12.9404L5.20308 12.1904H5.20308L5.19974 12.9404ZM6.68568 13.697C7.09989 13.6989 7.43717 13.3646 7.43902 12.9504C7.44086 12.5362 7.10658 12.1989 6.69237 12.1971L6.68568 13.697ZM2.74615 13.5127L2.15302 13.0537L2.15302 13.0537L2.74615 13.5127ZM3.01202 13.2468L2.553 12.6537L2.553 12.6537L3.01202 13.2468ZM9 0.75C4.44365 0.75 0.75 4.44365 0.75 9H2.25C2.25 5.27208 5.27208 2.25 9 2.25V0.75ZM9 17.25C13.5563 17.25 17.25 13.5563 17.25 9H15.75C15.75 12.7279 12.7279 15.75 9 15.75V17.25ZM2.73058 14.3628C4.24255 16.1288 6.49074 17.25 9 17.25V15.75C6.9473 15.75 5.10919 14.8347 3.87004 13.3872L2.73058 14.3628ZM15.2694 3.63724C13.7575 1.87116 11.5093 0.75 9 0.75V2.25C11.0527 2.25 12.8908 3.16535 14.13 4.61276L15.2694 3.63724ZM14.742 0.753346L14.7487 2.24263L16.2486 2.23594L16.242 0.746654L14.742 0.753346ZM12.742 4.24929L11.2527 4.24265L11.246 5.74263L12.7353 5.74928L12.742 4.24929ZM14.7487 2.24263C14.7514 2.8541 14.752 3.24933 14.7198 3.54478C14.6891 3.82513 14.6369 3.91916 14.5991 3.96798L15.7854 4.88602C16.0619 4.52864 16.1656 4.12265 16.2109 3.70762C16.2546 3.30767 16.2512 2.81214 16.2486 2.23594L14.7487 2.24263ZM12.7353 5.74928C13.3115 5.75185 13.807 5.7552 14.207 5.71152C14.622 5.66619 15.028 5.56258 15.3854 5.286L14.4673 4.09975C14.4185 4.13753 14.3245 4.18977 14.0441 4.22038C13.7487 4.25265 13.3535 4.25202 12.742 4.24929L12.7353 5.74928ZM14.5991 3.96798C14.5609 4.01731 14.5167 4.06157 14.4673 4.09975L15.3854 5.286C15.5351 5.17013 15.6695 5.03574 15.7854 4.88602L14.5991 3.96798ZM3.19638 17.1863L3.18973 15.6971L1.68975 15.7038L1.69639 17.193L3.19638 17.1863ZM5.19639 13.6904L6.68568 13.697L6.69237 12.1971L5.20308 12.1904L5.19639 13.6904ZM3.18973 15.6971C3.187 15.0856 3.18638 14.6904 3.21864 14.3949C3.24926 14.1146 3.30149 14.0205 3.33928 13.9717L2.15302 13.0537C1.87645 13.411 1.77283 13.817 1.7275 14.2321C1.68383 14.632 1.68718 15.1276 1.68975 15.7038L3.18973 15.6971ZM5.20308 12.1904C4.62688 12.1878 4.13136 12.1845 3.73141 12.2282C3.31638 12.2735 2.91038 12.3771 2.553 12.6537L3.47104 13.8399C3.51986 13.8022 3.61389 13.7499 3.89425 13.7193C4.18969 13.687 4.58492 13.6877 5.19639 13.6904L5.20308 12.1904ZM3.33928 13.9717C3.37745 13.9224 3.42171 13.8781 3.47104 13.8399L2.553 12.6537C2.40328 12.7696 2.26889 12.9039 2.15302 13.0537L3.33928 13.9717Z" fill="#666666"/>
          </svg>

        </button>
      </div>
      <div class="count_btn">
        <div>
          {{ $store.state.transactions.current_page }} дан {{$store.state.transactions.total_pages}}
        </div>
        <div class="count_btns">
          <button @click="prev()">

            <svg width="7" height="12" viewBox="0 0 7 12" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M6 1L1 6L6 11" stroke="#666666" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
            </svg>

          </button>
          <span>{{per_page}}</span>
          <button @click="next()">

            <svg width="7" height="12" viewBox="0 0 7 12" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M1 1L6 6L1 11" stroke="#666666" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
            </svg>

          </button>
        </div>
      </div>
    </div>
    <div class="no_data" v-if="$store.state.transactions.length==0">
      <img src="../../assets/image/malumot.png" alt="">
    </div>
    <div class="table_main parent" v-else>
      <div class="all_money">
        <div class="tb_head">
          Умумий ажратилган маблағ
        </div>
        <div class="count" >
          {{$store.state.price.allocated_amount.replace(/\B(?=(\d{3})+(?!\d))/g, " ")}}
        </div>
      </div>
      <div class="f_table">
        <table class="table-striped">
          <thead>
          <tr>
            <th class="col1">Буйруқ</th>
            <th class="col2">Тадбир номи</th>
            <th class="col2">Ташкилот номи</th>
            <th class="col3">Ажратилган маблағ</th>
            <th class="col4">Касса</th>
            <th class="col5">Хисобот топширилди</th>
            <th class="col6">Қайтарилган маблағлар</th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="(tr,t) in $store.state.transactions.results" :key="t">
            <td class="col1">
              <textarea type="text" v-model="tr.order"  @keydown.esc="deletes(tr)" v-on:keyup.enter="edittransactions5(tr)" :class="{'disebled_none_class':$store.state.auth.user.role==1||$store.state.auth.user.role==2}"></textarea>
            </td>
            <td class="col2">
              <textarea type="text"  v-model="tr.event_name" v-on:keyup.enter="edittransactions5(tr)" :class="{'disebled_none_class':$store.state.auth.user.role==1||$store.state.auth.user.role==2}"></textarea>
            </td>

            <td class="col3">
              <model-list-select
                  :list="options"
                  v-model="tr.organization"
                  option-value="code"
                  option-text="name"
                  placeholder="Ташкилотни танланг"
                  @input="edittransactions5(tr)"
                  :class="{'disebled_none_class':$store.state.auth.user.role==1||$store.state.auth.user.role==2}"
              >
              </model-list-select>
            </td>
            <td class="col3">
              <textarea type="text" id=""  @input="tr.planned_amount.replace(/\B(?=(\d{3})+(?!\d))/g, ' ')" v-bind:value="tr.planned_amount.replace(/\B(?=(\d{3})+(?!\d))/g, ' ')"  v-on:keyup.enter="edittransactions(tr,$event.target.value)" :class="{'disebled_none_class':$store.state.auth.user.role==1||$store.state.auth.user.role==2}" ></textarea>
            </td>
            <td class="col4">
              <textarea type="text"  @input="tr.actual_amount.replace(/\B(?=(\d{3})+(?!\d))/g, ' ')" :value="tr.actual_amount.replace(/\B(?=(\d{3})+(?!\d))/g, ' ')" v-on:keyup.enter="edittransactions1(tr,$event.target.value)" :class="{'disebled_none_class':$store.state.auth.user.role==1||$store.state.auth.user.role==3}"></textarea>
            </td>
            <td class="col5">
              <textarea type="text"  @input="tr.spent_amount.replace(/\B(?=(\d{3})+(?!\d))/g, ' ')" :value="tr.spent_amount.replace(/\B(?=(\d{3})+(?!\d))/g, ' ')" v-on:keyup.enter="edittransactions2(tr,$event.target.value)" :class="{'disebled_none_class':$store.state.auth.user.role==1||$store.state.auth.user.role==4}"></textarea>
            </td>
            <td>
              <textarea type="text" :value="tr.returned_amount.replace(/\B(?=(\d{3})+(?!\d))/g, ' ')"  @input="checkInput(tr)" v-on:keyup.enter="edittransactions3(tr,$event.target.value)" :class="{'disebled_none_class':$store.state.auth.user.role==1||$store.state.auth.user.role==3}"></textarea>
            </td>
          </tr>

          <tr   >
            <td class="col1">
              <textarea type="text" v-model="order"  v-on:keyup.enter="transactions()" :class="{'disebled_none_class':$store.state.auth.user.role==1||$store.state.auth.user.role==2}"></textarea>
            </td>
            <td class="col2">
              <textarea type="text"  v-model="event_name" v-on:keyup.enter="transactions()" :class="{'disebled_none_class':$store.state.auth.user.role==1||$store.state.auth.user.role==2}"></textarea>
            </td>
            <td class="col3">
              <model-list-select
                  :list="options"
                  v-model="selected"
                  option-value="code"
                  option-text="name"
                  placeholder="Ташкилотни танланг"
                  @input="onChange(selected)"
                  :class="{'disebled_none_class':$store.state.auth.user.role==1||$store.state.auth.user.role==2}"
              >
              </model-list-select>

            </td>
            <td class="col3">
              <textarea type="text" id="" v-model="planned_amount"  v-on:keyup.enter="transactions()" :class="{'disebled_none_class':$store.state.auth.user.role==1||$store.state.auth.user.role==2}" ></textarea>
            </td>
            <td class="col4">
              <textarea type="text"  v-model="actual_amount" v-on:keyup.enter="transactions()" :class="{'disebled_none_class':$store.state.auth.user.role==1||$store.state.auth.user.role==3}"></textarea>
            </td>
            <td class="col5">
              <textarea type="text" v-model="spent_amount" v-on:keyup.enter="transactions()" :class="{'disebled_none_class':$store.state.auth.user.role==1||$store.state.auth.user.role==4}"></textarea>
            </td>
            <td>
              <textarea type="text" v-model="returned_amount" v-on:keyup.enter="transactions()" :class="{'disebled_none_class':$store.state.auth.user.role==1||$store.state.auth.user.role==3}"></textarea>
            </td>
          </tr>

          </tbody>
        </table>
      </div>
      <div class="all_money">
        <div class="tb_head">
          Қолдиқ
        </div>
        <div class="count">
          {{$store.state.balance.total_remained_amount.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")}}
        </div>
      </div>
    </div>
  </div>
  <div>
    <div class="footer_main">
      <div class="statistic_footer">
        <div class="all_money_main">Жами</div>
        <div class="sum_child">
          <div class="money_sum">
            {{this.$store.state.aj_mab.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")}}
          </div>
          <div class="money_sum">
            {{this.$store.state.kassa.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")}}
          </div>
          <div class="money_sum">
            {{this.$store.state.xis_top.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")}}
          </div>
          <div class="money_sum">
            {{this.$store.state.qay_mab.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")}}
          </div>
        </div>
      </div>
    </div>
  </div>
</div>
</template>
<script>
import 'vue-search-select/dist/VueSearchSelect.css'
import {ModelListSelect} from 'vue-search-select'
export default {
  components:{
    ModelListSelect
  },
  data(){
    return{
      qay_mab:0,
      xis_top:0,
      kassa:0,
      aj_mab:0,
      options: [],
      objectItem: {},
      organisations:[],
      order:'',
      event_name:"",
      planned_amount:'',
      actual_amount:'',
      spent_amount:'',
      returned_amount:'',
      organization:'',
      balance:'',
      organiz:'',
      per_page:1,
      selected:''
    }
  },
  mounted() {
    var slider = document.querySelector('.parent');
    let mouseDown = false;
    let startX, scrollLeft;

    let startDragging = function (e) {
      mouseDown = true;
      startX = e.pageX - slider.offsetLeft;
      scrollLeft = slider.scrollLeft;
    };
    let stopDragging = function () {
      mouseDown = false;
    };
    if(slider) {
      slider.addEventListener('mousemove', (e) => {
        e.preventDefault();
        if (!mouseDown) {
          return;
        }
        const x = e.pageX - slider.offsetLeft;
        const scroll = x - startX;
        slider.scrollLeft = scrollLeft - scroll;
      });
    }

// Add the event listeners
    if(slider) {
      slider.addEventListener('mousedown', startDragging, false);
      slider.addEventListener('mouseup', stopDragging, false);
      slider.addEventListener('mouseleave', stopDragging, false);
    }
    this.$axiosDefault.get(`organizations?limit=1000`,
        {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`
          }
        })
        .then(response=>{
          this.organisations=response.data.results
          this.organisations.forEach(el=>{
            this.options.push(
                {
                  code:el.id,
                  name:`inn-${el.inn}  ${el.title}`
                })
          })
        })



  },
  computed: {
    selectedID: function () {
      return this.organiz
    }
  },
  methods:{
    getReload(){
       this.$router.go()
    },
    checkInput(tr){
      console.log(tr)
      if(Number(tr.returned_amount)>Number(this.$store.state.price.allocated_amount)){
        this.$toast("Эслатма сиз ажратилган маблағдан кўп миқдорда маблағ киритдингиз", {
          timeout: 2000,
          type:"error"
        });
        tr.returned_amount=''
      }
    },
    prev(){

      if(this.per_page>1){
        this.per_page--

        this.$axiosDefault.get(`/sports/${this.$store.state.price.sport}/balances/${this.$store.state.price.id}/transactions?page=${this.per_page}`,
            {
              headers: {
                'Authorization': `Bearer ${localStorage.getItem('token')}`
              }
            })
            .then(tran=>{
              this.$store.state.transactions=tran.data
            })
      }
    },
    next(){
      if(this.per_page<this.$store.state.transactions.total_pages){
        this.per_page++

        this.$axiosDefault.get(`/sports/${this.$store.state.price.sport}/balances/${this.$store.state.price.id}/transactions?page=${this.per_page}`,
            {
              headers: {
                'Authorization': `Bearer ${localStorage.getItem('token')}`
              }
            })
            .then(tran=>{
              this.$store.state.transactions=tran.data
            })
      }
    },


    transactions(){
      this.$axiosDefault.post(`/sports/${this.$store.state.price.sport}/balances/${this.$store.state.price.id}/transactions/`,
          {
            "order":this.order,
              "planned_amount":Number(this.planned_amount),
              "actual_amount":Number(this.actual_amount),
             "spent_amount":Number(this.spent_amount),
              "returned_amount":Number(this.returned_amount),
              "organization":this.organiz
          },
          {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('token')}`
            }
          })
          .then(()=>{
            this.order=''
            this.$axiosDefault.get(`/sports/${this.$store.state.price.sport}/balances/${this.$store.state.price.id}/transactions?page=${this.per_page}`,
                {
                  headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`
                  }
                })
            .then(tran=>{
              this.$store.state.transactions=tran.data
              this.organiz=''
            })
          })
    },
    onChange(selected){

      let isBoss = confirm("Rostan ham shu tashkilotmi?");
      if (isBoss == true) {
        this.$axiosDefault.post(`/sports/${this.$store.state.price.sport}/balances/${this.$store.state.price.id}/transactions/`,
            {
              "order":this.order,
              "planned_amount":Number(this.planned_amount),
              "actual_amount":Number(this.actual_amount),
              "spent_amount":Number(this.spent_amount),
              "returned_amount":Number(this.returned_amount),
              "organization":selected
            },
            {
              headers: {
                'Authorization': `Bearer ${localStorage.getItem('token')}`
              }
            })
            .then(()=>{

              this.$toast("Малумот киритилди", {
                timeout: 2000,
                type:"success"
              });
              this.$axiosDefault.get(`/sports/${this.$store.state.price.sport}/balances/${this.$store.state.price.id}/transactions?page=${this.per_page}`,
                  {
                    headers: {
                      'Authorization': `Bearer ${localStorage.getItem('token')}`
                    }
                  })
                  .then(tran=>{
                    this.$store.state.transactions=tran.data
                    this.organiz=''
                  })
            })

            .catch(()=>{
              this.$toast("Малумот киритишда хатолик бор", {
                timeout: 2000,
                type:"error"
              });
            })

      }

    },

    deletes(item){
      console.log(this.$store.state.auth.user.role)
      if(this.$store.state.auth.user.role==1){
        let isBoss = confirm("Rostan ham o'chirmoqchimisz?");
        if (isBoss == true) {
          this.$axiosDefault.delete(`/sports/${this.$store.state.price.sport}/balances/${this.$store.state.price.id}/transactions/${item.id}/`,
              {
                headers: {
                  'Authorization': `Bearer ${localStorage.getItem('token')}`
                }
              })
              .then(() => {
                this.$toast("Малумот ўчирилди", {
                  timeout: 2000,
                  type: "success"
                });
                this.$store.state.qay_mab = 0
                this.$store.state.xis_top = 0
                this.$store.state.kassa = 0
                this.$store.state.aj_mab = 0

                this.$axiosDefault.get(`/sports/${this.$store.state.price.sport}/balances/${this.$store.state.price.id}/transactions?page=${this.per_page}`,
                    {
                      headers: {
                        'Authorization': `Bearer ${localStorage.getItem('token')}`
                      }
                    })
                    .then(tran => {
                      this.$store.state.transactions = tran.data
                      tran.data.results.forEach(el => {
                        this.$store.state.aj_mab = this.$store.state.aj_mab + Number(el.planned_amount)
                        this.$store.state.kassa = this.$store.state.kassa + Number(el.actual_amount)
                        this.$store.state.xis_top = this.$store.state.xis_top + Number(el.spent_amount)
                        this.$store.state.qay_mab = this.$store.state.qay_mab + Number(el.returned_amount)
                      })
                    })
                this.$axiosDefault.get(`/sports/${this.$store.state.price.sport}/balances/${this.$store.state.price.id}?page=${this.per_page}`,
                    {
                      headers: {
                        'Authorization': `Bearer ${localStorage.getItem('token')}`
                      }
                    })
                    .then(balance => {
                      this.$store.state.balance = balance.data
                    })

              })
              .catch(() => {
                this.$toast("Малумот киритишда хатолик бор", {
                  timeout: 2000,
                  type: "error"
                });
              })
        }
      }

    },

    edittransactions5(item){
      console.log(item)
        this.$axiosDefault.patch(`/sports/${this.$store.state.price.sport}/balances/${this.$store.state.price.id}/transactions/${item.id}/`,
            {
              balance: Number(item.balance),
              order: item.order,
              organization: item.organization,
              event_name:item.event_name
            },
            {
              headers: {
                'Authorization': `Bearer ${localStorage.getItem('token')}`
              }
            })
            .then(() => {
              this.$toast("Малумот киритилди", {
                timeout: 2000,
                type: "success"
              });
              this.$store.state.qay_mab=0
              this.$store.state.xis_top=0
              this.$store.state.kassa=0
              this.$store.state.aj_mab=0

              this.$axiosDefault.get(`/sports/${this.$store.state.price.sport}/balances/${this.$store.state.price.id}/transactions?page=${this.per_page}`,
                  {
                    headers: {
                      'Authorization': `Bearer ${localStorage.getItem('token')}`
                    }
                  })
                  .then(tran => {
                    this.$store.state.transactions = tran.data
                    tran.data.results.forEach(el=>{
                      this.$store.state.aj_mab=this.$store.state.aj_mab+Number(el.planned_amount)
                      this.$store.state.kassa=this.$store.state.kassa+Number(el.actual_amount)
                      this.$store.state.xis_top=this.$store.state.xis_top+Number(el.spent_amount)
                      this.$store.state.qay_mab=this.$store.state.qay_mab+Number(el.returned_amount)
                    })
                  })
              this.$axiosDefault.get(`/sports/${this.$store.state.price.sport}/balances/${this.$store.state.price.id}?page=${this.per_page}`,
                  {
                    headers: {
                      'Authorization': `Bearer ${localStorage.getItem('token')}`
                    }
                  })
                  .then(balance => {
                    this.$store.state.balance = balance.data
                  })

            })
            .catch(() => {
              this.$toast("Малумот киритишда хатолик бор", {
                timeout: 2000,
                type: "error"
              });
            })

    },
    edittransactions(item,value){
