<script>
import axios from "axios";
export default {
  components: { },
  data(){
    return{
      statistics:[],
      statisticsId:[],
      qay_mab:0,
      xis_top:0,
      kassa:0,
      aj_mab:0,
      qoldiq:0,
      all_moneys:0,
      per_page:1,
      itId:0,
      statsExel:[],
      mone_money:[],
      actions:[],
      itemChild:{}
    }
  },
  mounted() {
    axios.get(`https://backend-moliya.sport.uz/actions/history`,
        {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`
          }
        })
    .then(res=>{
      console.log(res.data,"knjdsnakj")
      this.actions=res.data
    })


  },
  methods:{
    itemChildFunc(item){
      this.itemChild=item
    },
    prev(){
      if(this.per_page>1){
        this.per_page--
        this.$axiosDefault.get(`https://backend-moliya.minsport.uz/actions/history?page=${this.per_page}`,
            {
              headers: {
                'Authorization': `Bearer ${localStorage.getItem('token')}`
              }
            })
            .then(stats=> {
              this.actions = stats.data
            })
      }
    },
    next(){
      if(this.per_page<this.actions.total_pages){
        this.per_page++
        this.$axiosDefault.get(`https://backend-moliya.minsport.uz/actions/history?page=${this.per_page}`,
            {
              headers: {
                'Authorization': `Bearer ${localStorage.getItem('token')}`
              }
            })
            .then(stats=> {
              this.actions = stats.data
            })
      }
    },
    getReload() {
      this.$router.go()
    },
  }
}
</script>
<style>
.actions_button{
  background: none;
  outline: none;
  border: 0;
}
#modal-1 footer{
  display: none;
}
</style>
